.Itemwrapper {
    width: 100%;
    height: 100%;
    background-color: rgb(255, 255, 255);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 10% 0;
    border: 1px solid hsl(0, 0%, 0%, 0.2);
    box-shadow: 0px 4px 10px hsl(0, 0%, 0%, 0.1);
}

.Itemwrapper:hover {
    transition: box-shadow 250ms ease-in-out;
    box-shadow: 0px 8px 20px hsl(0, 0%, 0%, 0.2);
}

.icon_wrapper {
    height: 50%;
    width: 100%;
    clip-path: circle(22% at 50% 50%);
    background-color: hsl(0, 0%, 21%, 0.8);
    display: flex;
    color: #fff;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.Icon {
    font: 500 2rem sans-serif;
}

.ulItem {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    gap: 1%;
    margin-bottom: 2rem;
    font: 300 0.7rem sans-serif;
    color: #000;
}

.ulItem li {
    width: 100%;
    display: flex;
    flex-direction: row;
    gap: 0%;
    font-size: 0.9rem;
    justify-content: center;
    align-items: center;
}

.liIcon {
    font-size: 1.6rem;
}

.start {
    width: 60%;
    height: 18%;
    background-color: hsl(0, 0%, 21%);
    color: #fff;
    font: 500 0.81rem sans-serif;
    cursor: pointer;
}

.start:hover {
    transition: transform, color 1ms, box-shadow 350ms ease-in-out, background 250ms ease-in-out;
    transform: scale(1.0125);
    background-color: #fff;
    color: #353535;
    border: none;
    box-shadow: 0 4px 8px hsl(0, 0%, 0%, 0.41);
}