.sidemenuWrapper {
  position: sticky;
  width: max(15vw, 10rem);
  height: 100vh;
  top: 0;
  bottom: 0;
  left: 0%;
  background-color: hsl(0, 0%, 0%, 0.1);
  border-right: 1px solid hsl(0, 0%, 0%, 0.1);
  box-shadow: 0px 2px 3px hsl(0, 0%, 0%, 0.4);
}

.header > * {
  display: grid;
  place-items: center;
  background-color: rgb(201, 199, 199);
}
.header img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.menuContent {
  width: 100%;
  padding: 1% 0.3vw;
  margin-top: 10vh;
}

.Sidemanu_li {
  margin: 1rem 0;
  width: 100%;
  cursor: pointer;
  display: flex;
}

.Sidemanu_li_Link {
  width: 100%;
  height: 100%;
  padding: 0.7rem 0.53rem;
  text-align: start;
}

.Sidemanu_li_Link.active {
  position: relative;
  width: 100%;
  background-color: var(--golden-color);
  border-radius: 0.31vw;
  color: rgb(32, 32, 32);
}

@media screen and (max-width: 600px) {
  .sidemenuWrapper {
    position: sticky;
    width: 100%;
    height: 15vh;
    top: 0;
    bottom: unset;
    right: 0%;
    left: 0%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0%;
  }
  .menuContent {
    margin: 0%;
    margin-left: 5vw;
  }
  .menuContent ul {
    display: flex;
    flex-direction: row;
    gap: 10vw;
  }
  .Sidemanu_li_Link.active::after {
    width: 100%;
    left: 20%;
  }
}
